"use client";
import Sidebar from "@/components/Proponent/Sidebar";
import Header from "@/components/Header";
import { useState } from "react";
// Button Components
import PrimaryButton from "@/components/Buttons/PrimaryButton";
import SecondaryButton from "@/components/Buttons/SecondaryButton";
import UtilityButton from "@/components/Buttons/UtilityButton";
import DangerButton from "@/components/Buttons/DangerButton";
// Text Components
import SecondaryHeader from "@/components/Texts/SecondaryHeader";
import TertiaryHeader from "@/components/Texts/TertiaryHeader";
import BodyText from "@/components/Texts/BodyText";
// Tabs
import TableList from "@/components/Tables/TableList";

export default function Students() {
  const [activeTab, setActiveTab] = useState("Non Reader");

  return (
    <div className="flex h-screen bg-white overflow-hidden">
      {/*---------------------------------Sidebar---------------------------------*/}
      <Sidebar />
      
      {/*---------------------------------Main Content---------------------------------*/}
      <div className="
        /* Mobile */
        flex-1 pt-16 flex flex-col overflow-hidden
        
      ">
        <Header title="Remedial" />
        <main className="flex-1 overflow-y-auto">
          <div className="
            /* Mobile */
            p-4 h-full
            
            /* Tablet */
            sm:p-5
            
            /* Desktop */
            md:p-6
          ">
            {/*---------------------------------Main Container---------------------------------*/}
            <div className="
              /* Mobile */
              bg-white rounded-lg shadow-md border border-gray-200 h-full min-h-[400px] 
              overflow-y-auto p-4
              
              /* Tablet */
              sm:p-5
              
              /* Desktop */
              md:p-6
            ">
              {/*---------------------------------Tabs Navigation---------------------------------*/}
              <div className="
                /* Mobile */
                flex flex-wrap gap-2 mb-6
                
                /* Tablet */
                sm:flex-nowrap sm:gap-3 sm:mb-8
                
                /* Desktop */
                md:mb-10 md:mt-2
              ">
                {['Non Reader', 'Syllable', 'Word', 'Sentence', 'Paragraph'].map(tab => (
                  <button
                    key={tab}
                    onClick={() => setActiveTab(tab)}
                    className={`
                      /* Mobile */
                      px-3 py-1.5 text-sm rounded-lg font-semibold focus:outline-none 
                      transition-all duration-150 shadow-sm border border-transparent
                      flex-grow text-center
                      
                      /* Tablet */
                      sm:px-4 sm:py-2 sm:text-base sm:flex-grow-0
                      
                      /* Desktop */
                      md:px-5
                      ${
                        activeTab === tab
                          ? 'bg-[#013300] text-white shadow-md'
                          : 'bg-gray-100 text-[#013300] hover:ring-2 hover:ring-[#013300] hover:scale-[1.03] hover:shadow-md'
                      }
                    `}
                  >
                    {tab}
                  </button>
                ))}
              </div>
              
              {/*---------------------------------Tab Content---------------------------------*/}
              <div className="mt-4 sm:mt-6">
                <TableList
                  columns={[
                    { key: "no", title: "No#" },
                    { key: "title", title: "Remedial Title" },
                    { key: "level", title: "Phonemic Level" },
                    { key: "date", title: "Date to Use" },
                    { key: "status", title: "Status" },
                  ]}
                  data={[
                    { id: 1, no: 1, title: "Vowels", level: "Non-Reader", date: "2025-07-15", status: "Completed" },
                    { id: 2, no: 2, title: "Consonants", level: "Syllable", date: "2025-07-16", status: "Pending" },
                  ]}
                  actions={(row) => (
                    <>
                      <UtilityButton small>Play</UtilityButton>
                      <DangerButton small>Delete</DangerButton>
                    </>
                  )}
                  pageSize={10}
                />
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}